.about {
  padding-bottom: 80px;

  &__text {
    grid-column: 12 span;
    gap: 24px 20px;
    margin-bottom: 32px;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__title {
    grid-column: 4 span;
    margin-bottom: 32px;
  }

  &__descr {
    grid-column: 6 span;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__images {
    grid-column: 12 span;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 6 span;
      row-gap: 15px;
    }
  }

  &__image {
    grid-column: 6 span;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
    
  }
}
