.footer {
  background-color: #333;

  &__logo {
    grid-column: 6 span;

    @include tablet {
      grid-column: 8 span;
      margin-bottom: 30px;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__top {
    padding: 56px 0;
  }

  &__bottom {
    padding: 16px 0 24px 0;
    align-items: center;
    justify-content: center;
  }

  &__list {
    grid-column: 2 span;
    grid-template-columns: repeat(2, 1fr);

    @include mobile {
      grid-column: 3 span;
      margin-bottom: 60px;
    }
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    color: var(--white-color);
    font-size: 16px;
    line-height: 120%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #ffffffc4;
    }

    &:active {
      color: #ffffff93;
    }
  }

  &__copy {
    color: var(--white-color);
    font-size: 14px;
    line-height: 120%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #ffffffc4;
      text-decoration: underline;
    }

    &:active {
      color: #ffffff93;
      text-decoration: underline;
    }
  }
}

.footer__link.focus-visible {
  outline: 1.5px solid var(--white-color);
}

.footer__copy.focus-visible {
  outline: 1.5px solid var(--white-color);
}
