.products {
  padding-bottom: 80px;

  &__container {
    flex-direction: column;
    margin-bottom: 32px;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__list {
    row-gap: 25px;

    @include mobile {
      padding: 0 25px;
    }
  }

  &__item {
    grid-column: 4 span;
    flex-direction: column;
    background: #f5f5f5;
    padding: 32px 24px;
    min-height: 435px;

    @include small-tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
      align-items: center;
    }
  }

  &__top {
    flex: 1 1 auto;
  }

  &__image {
  }

  &__label {
    color: var(--text-color-dark);
    font-size: 28px;
    font-weight: 600;
    line-height: 120%;
    margin-bottom: 12px;
  }

  &__button {
    span {
      color: var(--text-color-blue);
      font-size: 20px;
      line-height: 120%;
      margin-right: 8px;
      transition: color 0.3s ease-in-out;
    }

    svg {
      path {
        fill: var(--accent-color);
        transition: fill 0.3s ease-in-out;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--text-color-blue-hover);
      }

      svg {
        path {
          fill: var(--text-color-blue-hover);
        }
      }
    }

    &:active {
      span {
        color: var(--text-color-blue-active);
      }

      svg {
        path {
          fill: var(--text-color-blue-active);
        }
      }
    }
  }

  &__bottom {
    flex: 0 1 auto;
  }
}

.products__button.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}
