.offices {
  padding-bottom: 80px;

  @include tablet {
    display: none;
  }

  &__container {
    flex-direction: column;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__map {
    position: relative;
  }

  &__image {
    margin-left: 3.57px;
    margin-right: 24.53px;
    width: 100%;
    height: 100%;
  }

  &__city {
    position: absolute;
    display: flex;
    align-items: center;

    img {
      margin-right: 4px;
    }

    span {
      color: var(--text-color-dark);
      font-size: 12px;
      line-height: 120%;
    }

    &--rostov {
      left: 6%;
      bottom: 24%;
    }

    &--volgograd {
      left: 8.5%;
      bottom: 21%;
    }

    &--samara {
      left: 16%;
      bottom: 25%;
    }

    &--kazan {
      left: 18%;
      bottom: 29%;
    }

    &--ufa {
      left: 20.8%;
      bottom: 22%;
    }

    &--nijnevartovsk {
      left: 33%;
      bottom: 34%;
    }

    &--vladivastok {
      right: 2%;
      bottom: 13%;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--text-color-dark-hover);
        text-decoration: underline;
      }
    }

    &:active {
      span {
        color: var(--text-color-dark-active);
        text-decoration: underline;
      }
    }
  }
}

.offices__city.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}
