.header {
  padding-top: 35px;
  padding-bottom: 80px;

  &__top {
    justify-content: space-between;
    padding-bottom: 32px;
    margin-bottom: 16px;
    border-bottom: 1px solid #888;
  }

  &__contact {

    @include small-tablet {
      flex-direction: column;
      justify-content: end;
    }
  }

  &__email {
    margin-right: 20px;

    @include small-tablet {
      margin-right: 0;
      order: 2;
      text-align: right;
    }
  }

  &__text {
    margin: 0;
    color: var(--text-color-dark);
    text-align: right;
    font-size: 14px;
    line-height: 120%;


  }

  &__mail {
    color: var(--text-color-blue);
    text-align: right;
    font-size: 20px;
    font-weight: 600;
    text-decoration-line: underline;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-blue-hover);
    }

    &:active {
      color: var(--text-color-blue-active);
    }
  }

  &__number {
    @include small-tablet {
      order: 1;
      margin-bottom: 8px;
      text-align: right;
    }
  }

  &__num {
    color: var(--text-color-blue);
    text-align: right;
    font-size: 20px;
    font-weight: 600;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-blue-hover);
    }

    &:active {
      color: var(--text-color-blue-active);
    }
  }

  &__bottom {
    justify-content: space-between;
  }

  &__recall {
    padding-left: 20px;
    background: url(../img/header/phone.svg) left center no-repeat;
    color: var(--text-color-blue);
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      text-decoration: underline;
    }

    &:active {
      color: var(--text-color-blue-active);
    }
  }
}

.nav {
  @include notebook {
    position: fixed;
    left: 0;
    top: 0;
    width: 80%;
    height: 100vh;
    padding: 110px;
    background-color: #272727;
    z-index: 100;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    transform: translateX(-100%);
    visibility: hidden;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;

    @include mobile {
      padding-left: 50px;
    }
  }

  &__list {
    @include notebook {
      flex-direction: column;
    }
  }

  &__item {
    margin-right: 44px;

    &:last-child {
      margin-right: 0;
    }

    @include notebook {
      margin-right: 0;
      margin-bottom: 44px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    color: var(--text-color-dark);
    font-size: 16px;
    font-weight: 400;
    line-height: 120%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-dark-hover);
    }

    &:active {
      color: var(--text-color-dark-active);
    }

    @include notebook {
      color: var(--white-color);

      @include hover {
        color: #ffffffc4;
      }

      &:active {
        color: #ffffff93;
      }
    }
  }
}

.header__mail.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.header__num.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.nav__link.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.header__recall.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}

.logo {
  flex-shrink: 0;
  padding-right: 20px;

  @include mobile {
    flex-shrink: 1;
  }
}
