.major {
  margin-bottom: 80px;

  &__top {
    grid-column: 12 span;
    margin-bottom: 32px;
    align-items: center;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__bottom {
    grid-column: 12 span;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__title {
    grid-column: 6 span;
    font-size: 60px;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 3 span;
      font-size: 44px;
    }

    @include mobile {
      grid-column: 6 span;
      margin-bottom: 15px;
    }
  }

  &__descr {
    grid-column: 4 span;

    @include tablet {
      grid-column: 3 span;
    }

    @include small-tablet {
      grid-column: 2 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }
}
