.news {
  margin-bottom: 80px;

  &__container {
    flex-direction: column;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__list {
    row-gap: 24px;
  }

  &__item {
    grid-column: 4 span;
    flex-direction: column;

    @include small-tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__bottom {
    background-color: #f5f5f5;
    flex-direction: column;
    padding: 16px;
    height: 100%;
    justify-content: space-between;
  }

  &__descr {
    margin: 0;
    color: var(--text-color-dark);
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 20px;
  }

  &__footer {
    justify-content: space-between;
  }

  &__button {
    color: var(--text-color-blue);
    font-size: 16px;
    line-height: 120%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--text-color-blue-hover);
    }

    &:active {
      color: var(--text-color-blue-active);
    }
  }

  &__date {
    color: #888;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
  }
}

.news__button.focus-visible {
  outline: 1.5px solid var(--text-color-dark);
}
